.footer {
   & &__container {
      display: flex;
      justify-content: space-between;
      @include avalue("padding-top", 80, 30);
      @include avalue("padding-bottom", 80, 30);
      padding-left: 25px;
      padding-right: 25px;
      gap: 30px;
      color: rgba(#fff, 0.5);
      border-top: 1px solid rgba(#ebebeb, 0.1);
      font-size: 15px;
      line-height: math.div(21, 15);
      position: relative;
      @media (max-width: 1024px) {
         flex-wrap: wrap;
         justify-content: start;
      }
      @media (max-width: 480px) {
         justify-content: space-around;
         text-align: center;
         padding-left: 15px;
         padding-right: 15px;
      }
      @media (max-width: 390px) {
        padding-bottom: 30px;
        padding-top: 30px;
      }
   }
   &__logo {
      margin-bottom: auto;
      opacity: 0.5;
      display: block;
      @media (any-hover: hover) {
         &:hover {
            opacity: 1;
         }
      }
   }
   &__about {
   }
   &__copyright {
      margin-bottom: 20px;
   }
   &__site-desc {
      max-width: 350px;
   }
   &__menu {
      max-width: 470px;
      margin-top: auto;
   }
   &__menu-list {
      display: flex;
      flex-wrap: wrap;
      gap: 40px 45px;
      @media (max-width: 480px) {
         text-align: center;
         justify-content: center;
      }
      @media (max-width: 390px) {
        gap: 25px 30px;
      }
   }
   &__menu-list-item {
    @media (max-width: 390px) {
        flex-basis: 40%;
        &:nth-child(2n) {
            text-align: left;
        }
        &:nth-child(2n+1) {
            text-align: right;
        }
    }
      a {
         @media (any-hover: hover) {
            &:hover {
               color: #fff;
            }
         }
      }
   }
   &__socials {
      margin-top: auto;
      @media (max-width: 390px) {
         width: 100%;
      }
      ul {
         display: flex;
         gap: 25px;
         @media (max-width: 390px) {
            justify-content: space-around;
         }
      }
      a {
         @media (any-hover: hover) {
            &:hover {
               &::before {
                  color: #fff;
               }
            }
         }
         &::before {
            color: rgba(#fff, 0.3);
            font-size: 18px;
            @media (max-width: 390px) {
               font-size: 30px;
            }
         }
      }
   }
}
