.roadmap {
   contain-intrinsic-size: 1px 2500px;
   @include avalue("padding-top", 150, 70);
   @include avalue("padding-bottom", 150, 70);
   color: #fff;
   position: relative;
   background: no-repeat center bottom
      url('data:image/svg+xml;utf-8,<svg viewBox="0 0 1920 1652" fill="none" xmlns="http://www.w3.org/2000/svg"><g opacity="0.6"><path opacity="0.2" d="M410.916 223.139L3.65625 0V232.894V469.446H1989.96V232.894L1876.56 170.708L1699.76 114.618L1553.44 104.863L1387.61 208.507L1342.49 232.894L1219.34 163.392L973.031 36.5802L863.291 92.6698L624.3 163.392L410.916 223.139Z" fill="url(%23paint0_linear_0_1)"/><path opacity="0.2" d="M410.917 139.005L0 313.371V517.001H1964.36V103.645L1699.76 267.036L1493.69 391.409L1416.87 356.048L1202.27 257.281L1186.42 260.939L1165.69 278.01L981.568 443.84L966.936 434.086L853.538 364.583L469.446 128.031H448.717L410.917 139.005Z" fill="url(%23paint1_linear_0_1)"/></g><g clip-path="url(%23clip0_0_1)"><path opacity="0.1" d="M128.239 1180.23C-56.4044 1176.26 -73.5707 1126.78 -128.682 1082.22L-151.658 1814.85L1928.36 1907.12L1960.16 893.096C1960.09 893.213 1907.01 985.553 1776.91 1016.32C1570.06 1065.24 1067.13 909.16 843.792 951.618C575.783 1002.57 340.753 1184.8 128.239 1180.23Z" fill="url(%23paint2_linear_0_1)"/><path opacity="0.1" d="M1656.26 1517.79C1852.87 1522 1872.79 1473.3 1932.92 1431.2L1932.92 2164.65L-283.533 2164.65L-283.533 1149.49C-283.466 1149.61 -230.06 1244.28 -92.6525 1280.81C125.831 1338.89 666.213 1205.15 902.456 1257.5C1185.95 1320.32 1429.96 1512.93 1656.26 1517.79Z" fill="url(%23paint3_linear_0_1)"/><ellipse opacity="0.1" cx="489" cy="1385.5" rx="161" ry="29.5" fill="url(%23paint4_linear_0_1)"/><ellipse opacity="0.1" cx="1451" cy="1157" rx="230" ry="35" fill="url(%23paint5_linear_0_1)"/><ellipse opacity="0.1" cx="1246.5" cy="1526" rx="150.5" ry="31" fill="url(%23paint6_linear_0_1)"/></g><defs><linearGradient id="paint0_linear_0_1" x1="996.808" y1="0" x2="996.808" y2="470.665" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D"/><stop offset="0.473094" stop-color="%233E4C3D" stop-opacity="0"/></linearGradient><linearGradient id="paint1_linear_0_1" x1="982.178" y1="103.645" x2="982.178" y2="517.001" gradientUnits="u