      #738471;
   @media (max-width: 980px) {
      background: no-repeat center bottom
         url('data:image/svg+xml;utf8,<svg viewBox="0 0 390 772" fill="none" xmlns="http://www.w3.org/2000/svg"><g clip-path="url(%23clip0_0_1)"><path opacity="0.1" d="M-448.253 482.214C-561.636 479.774 -572.177 449.383 -606.019 422.01L-620.127 872.033L657.133 928.708L676.66 305.84C676.619 305.912 644.022 362.633 564.137 381.531C437.115 411.581 128.286 315.708 -8.85879 341.788C-173.433 373.084 -317.756 485.022 -448.253 482.214Z" fill="url(%23paint0_linear_0_1)"/><path opacity="0.1" d="M490.043 689.558C610.777 692.148 623.008 662.233 659.932 636.369L659.932 1086.9L-701.109 1086.9L-701.109 463.33C-701.068 463.404 -668.273 521.555 -583.896 543.993C-449.734 579.669 -117.906 497.521 27.1621 529.678C201.244 568.266 351.086 686.576 490.043 689.558Z" fill="url(%23paint1_linear_0_1)"/><ellipse opacity="0.1" cx="364.004" cy="467.944" rx="141.234" ry="21.4989" fill="url(%23paint2_linear_0_1)"/><ellipse opacity="0.1" cx="238.428" cy="694.604" rx="92.4164" ry="19.0419" fill="url(%23paint3_linear_0_1)"/></g><g opacity="0.6" clip-path="url(%23clip1_0_1)"><path opacity="0.2" d="M105.273 78.2454L-37.1328 0.220703V81.6563V164.371H657.415V81.6563L617.763 59.9117L555.94 40.299L504.776 36.8881L446.791 73.129L431.015 81.6563L387.953 57.3535L301.827 13.0116L263.454 32.6244L179.887 57.3535L105.273 78.2454Z" fill="url(%23paint4_linear_0_1)"/><path opacity="0.2" d="M105.271 48.8255L-38.4141 109.796V180.999H648.459V36.4609L555.938 93.5938L483.882 137.083L457.021 124.718L381.981 90.1829L376.439 91.462L369.19 97.4311L304.809 155.417L299.693 152.006L260.041 127.703L125.736 44.9882H118.488L105.271 48.8255Z" fill="url(%23paint5_linear_0_1)"/></g><defs><linearGradient id="paint0_linear_0_1" x1="11.8315" y1="338.709" x2="58.5373" y2="712.721" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D"/><stop offset="1" stop-color="%233E4C3D" stop-opacity="0"/></linearGradient><linearGradient id="paint1_linear_0_1" x1="5.24785" y1="525.682" x2="5.24785" y2="821.139" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D"/><stop offset="1" stop-color="%233E4C3D" stop-opacity="0"/></linearGradient><linearGradient id="paint2_linear_0_1" x1="364.004" y1="446.445" x2="364.004" y2="489.443" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D" stop-opacity="0.2"/><stop offset="1" stop-color="%233E4C3D"/></linearGradient><linearGradient id="paint3_linear_0_1" x1="238.428" y1="675.562" x2="238.428" y2="713.646" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D" stop-opacity="0.2"/><stop offset="1" stop-color="%233E4C3D"/></linearGradient><linearGradient id="paint4_linear_0_1" x1="310.141" y1="0.220703" x2="310.141" y2="164.797" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D"/><stop offset="0.473094" stop-color="%233E4C3D" stop-opacity="0"/></linearGradient><linearGradient id="paint5_linear_0_1" x1="305.023" y1="36.4609" x2="305.023" y2="180.999" gradientUnits="userSpaceOnUse"><stop stop-color="%233E4C3D"/><stop offset="0.682292" stop-color="%233E4C3D" stop-opacity="0"/></linearGradient><clipPath id="clip0_0_1"><rect width="390" height="543" fill="white" transform="translate(0 229)"/></clipPath><clipPath id="clip1_0_1"><rect width="390" height="181" fill="white"/></clipPath></defs></svg>')
         #738471;
   }
   &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 611px;
      width: pc(681, $maxWidthContainer);
      background: no-repeat
         url('data:image/svg+xml;utf8,<svg viewBox="0 0 681 611" fill="none" xmlns="http://www.w3.org/2000/svg"><path opacity="0.15" d="M241 610C483.453 610 680 413.453 680 171C680 -71.453 483.453 -268 241 -268C-1.45303 -268 -198 -71.453 -198 171C-198 413.453 -1.45303 610 241 610Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path opacity="0.15" d="M42 112C108.274 112 162 58.2742 162 -8C162 -74.2742 108.274 -128 42 -128C-24.2742 -128 -78 -74.2742 -78 -8C-78 58.2742 -24.2742 112 42 112Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/></svg>');
      @media (max-width: 980px) {
         width: pc(460 - 95, 390);
      }
   }
   img {
      max-width: 100%;
      object-fit: cover;
   }
   &__header {
      color: #3b3633;
      text-align: left;
      float: left;
      // @include aminvalue('margin-top', -105, -75, 980);
      @include abetweenvalue("margin-top", -105, -75, 980, 0);
      @media (max-width: 980px) {
         float: none;
         margin-bottom: 70px;
         margin-left: 30px;
      }
   }
   &__title {
      font-family: SaolDisplay;
      @include avalue("font-size", 70, 45);
      line-height: 1;
      @include avalue("margin-bottom", 30, 20);
   }
   &__desc {
      font-weight: 500;
      @include avalue("font-size", 18, 15);
      max-width: 350px;
   }
   /* uncss:ignore */
   .roadmap__timeline-container {
      position: relative;
      margin-left: auto;
      margin-right: auto;
      @media (max-width: 980px) {
         max-width: 600px;
      }
   }
   .timeline__list {
      // margin-top: -18px;
   }

   .timeline__list-item {
      position: relative;
      @include avalue("padding-bottom", 50, 30);
      @media (min-width: 981px) {
        &:nth-child(1) {
            .timeline__card-item {
                margin-top: -100px;
                @media (max-width: 1368px) {
                    margin-top: -85px;
                }
            }
        }
         &:nth-child(2n + 1) {
            text-align: right;
         }
         &:nth-child(2) {
            @include aminvalue("margin-top", -370, -280, 980);
         }
         &:nth-child(3) {
            margin-top: -100px;
         }
         &:nth-child(4) {
            margin-top: -60px;
         }
         &:nth-child(5) {
            margin-top: -450px;
            @include aminvalue("margin-top", -450, -310, 980);
         }
         &:nth-child(6) {
            margin-top: -50px;
         }
         &:nth-child(7) {
            margin-top: -20px;
         }
         &:nth-child(8) {
            margin-top: -480px;
         }
         &:nth-child(9) {
            margin-top: -80px;
         }
         &:nth-child(10) {
            @include aminvalue("margin-top", 60, -50, 980);
         }
         &:nth-child(11) {
            margin-top: -80px;
         }
      }
      @media (max-width: 980px) {
         display: flex;
         flex-direction: column;
      }
   }
   .timeline {
      display: inline-block;
      width: 100%;
      max-width: 100%;
      margin: 0 auto;
      position: relative;
      @media (max-width: 390px) {
         margin-left: -10px;
      }
   }
   .timeline__line {
      width: 2px;
      background-color: transparent;
      position: absolute;
      top: 2px;
      left: 50%;
      bottom: 0;
      overflow: hidden;
      transform: translateX(-50%);
      @media (max-width: 980px) {
         left: 10px;
      }
      @media (max-width: 480px) {
         // transform: translate(-50%, 30px);
      }
   }
   .timeline__list {
   }

   .timeline__line-progress {
      display: block;
      width: 100%;
      height: 20%;
      background-color: #fff;
   }

   .timeline__card-box {
      display: flex;
      justify-content: center;
      @media (max-width: 980px) {
         justify-content: start;
         margin-left: 75px;
      }
      @media (max-width: 980px) {
         margin-left: 30px;
         opacity: 0;
         transition: opacity 1s;
      }
   }
   .timeline__list-item:nth-child(2n) .timeline__card-box {
   }
   .timeline__card-point-box {
      position: relative;
      z-index: 1;

      &::after {
         content: "";
         position: absolute;
         width: 75px;
         height: 9px;
         left: 100%;
         top: 22px;
         transition: opacity 0.5s;
         opacity: 0;
         background: no-repeat
            url('data:image/svg+xml;utf-8,<svg width="75" height="9" viewBox="0 0 75 9" fill="none" xmlns="http://www.w3.org/2000/svg"><circle r="4.5" transform="matrix(-1 0 0 1 4.5 4.5)" fill="white"/><circle r="3.5" transform="matrix(-1 0 0 1 30.5 4.5)" fill="white"/><circle r="2.5" transform="matrix(-1 0 0 1 52.5 4.5)" fill="white"/><circle r="2.5" transform="matrix(-1 0 0 1 72.5 4.5)" fill="white"/></svg>');
         @media (max-width: 1280px) {
            display: none;
         }
      }
      @media (max-width: 980px) {
         &::after {
            width: 20px;
            height: 20px;
            background: #fff;
            left: -30px;
            display: block;
            border-radius: 50%;
            top: 0;
         }
      }
   }
   .timeline__list-item:nth-of-type(2n) {
      .timeline__card-point-box {
         &::after {
            transform: rotate(180deg) translateX(100%);
            left: 0;
            @media (max-width: 980px) {
               left: -30px;
               transform: none;
            }
         }
      }
   }
   .timeline__list-item:nth-child(2n) .timeline__card-point-box {
      // margin: 0 -28px 0 14px;
   }

   .js-ag-active {
      .timeline__card-box {
         opacity: 1;
      }
      .timeline__card-point-box {
         &::before {
            content: "";
            position: absolute;
            width: 4px;
            z-index: -1;
            height: 100%;
            background-color: #738471;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            @media (max-width: 980px) {
               display: none;
            }
         }
         &:after {
            opacity: 1;
         }
      }
   }
   .timeline__card-point {
      @include avalue("font-size", 36, 20);
      font-weight: 500;
      line-height: 1.2;
      text-align: center;
      padding: 7px 0;
      width: 270px;
      word-spacing: 0.25em;
      @media (max-width: 980px) {
         text-align: left;
         width: auto;
         margin: -14px 0 0;
      }
      @media (max-width: 768px) {
         margin-top: -12px;
      }
      @media (max-width: 430px) {
         margin-top: -9px;
      }
      // @include amaxvalue('margin-top', -14, -10, 980);
   }
   .js-ag-active .timeline__card-point {
      // color: #1d1d1b;
   }
   .timeline__card-item {
      display: inline-block;
      width: pc(500, $maxWidthContainer);
    //   @include aminvalue("margin-top", -95, -75, 980);
      margin-top: -50px;
      opacity: 0;
      transition: transform 0.5s, opacity 0.5s;
      position: relative;
      @media (max-width: 1368px) {
      }
      @media (max-width: 1280px) {
         width: 35%;
         margin-top: -45px;
      }
      @media (max-width: 980px) {
         margin-top: 0;
         margin-left: 30px;
         width: auto;
      }
      @media (max-width: 979px) {
         margin-top: 0;
      }
 
   }
   .timeline__list-item {
      &:nth-child(2n) {
         .timeline__card-item {
            // margin-top: -40px;
         }
      }
   }
   .timeline__list-item:nth-child(2n) .timeline__card-item {
      transform: translateX(-200%);
      @media (max-width: 980px) {
         transform: translateX(200%);
      }
   }
   .timeline__list-item:nth-child(2n + 1) .timeline__card-item {
      transform: translateX(200%);
   }
   .js-ag-active.timeline__list-item:nth-child(2n + 1) .timeline__card-item,
   .js-ag-active.timeline__list-item:nth-child(2n) .timeline__card-item {
      opacity: 1;
      transform: translateX(0);
   }

   .timeline__card-img {
      width: 100%;
      height: auto;
      @media (max-width: 980px) {
         max-height: 400px;
      }
      @media (max-width: 768px) {
         max-height: 300px;
      }
      @media (max-width: 430px) {
         width: 100%;
         max-height: 150px;
      }
   }
   .timeline__card-info {
      // padding: 20px 30px;
      text-align: left;
   }
   .timeline__card-title {
      margin-bottom: 10px;
      font-weight: bold;
      @include avalue("font-size", 28, 24);
   }

   .timeline__card-desc {
      @include avalue("font-size", 20, 14);
      &:not(:last-child) {
         @include avalue("margin-bottom", 40, 15);
      }
   }

   @media only screen and (max-width: 979px) {
      .timeline__list-item:nth-child(2n) {
         text-align: left;
      }

      .timeline__card-point-box,
      .timeline__list-item:nth-child(2n) .timeline__card-point-box {
         //   margin: 0 0 0 8px;
      }

      .timeline__list-item:nth-child(2n + 1) .timeline__card-item,
      .timeline__list-item:nth-child(2n) .timeline__card-item {
         transform: translateX(200%);
      }

      .timeline__card-title {
         display: block;
      }
   }

   @media only screen and (max-width: 767px) {
      .timeline__card-img {
         height: auto;
         width: 100%;
      }
   }
   /* uncss:ignore end */
}
