.journey {
   @include avalue("padding-top", 120, 40);
   @include avalue("padding-bottom", 120, 40);
   color: #fff;
   &__container {
      position: relative;
   }
   &__title {
      font-family: SaolDisplay;
      @include aminvalue("font-size", 140, 64, 980);
      font-weight: 300;
      line-height: 1;
      position: absolute;
      z-index: 3;
      @media (max-width: 600px) {
         position: static;
         margin-bottom: 20px;
      }
   }
   &__slider {
      @include avalue("width", 640, 280);
      margin: 0 auto;
      perspective: 0;
   }
   &__controls {
      @media (max-width: 1440px) {
         display: flex;
         justify-content: center;
      }
      .swiper-navigation {
         display: flex;
         align-items: center;
         gap: 80px;
         position: absolute;
         bottom: 0;
         z-index: 5;
         @media (max-width: 1440px) {
            position: static;
            margin-top: 40px;
         }
         @media (max-width: 768px) {
            display: none;
         }
      }
      .swiper-button-prev {
      }
      .swiper-button-next {
      }
   }
   .slide {
      flex: 0 0 100%;
      &.swiper-slide-active {
         // opacity: 1;
      }
      &__image {
         border-radius: 50%;
         width: 100%;
         padding-bottom: 100%;
         position: relative;
         box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
         // transition: transform .5s;
         // transform: scale(math.div(480, 640));
         img {
            position: absolute;
            width: 100%;
            height: auto;
         }
      }
      &.swiper-slide-active {
         .slide__image {
            transform: none;
         }
      }
      &.swiper-slide-active {
         .slide__desc {
            opacity: 1;
         }
      }
      &__desc {
         padding: 0 pc(77, 640);
         opacity: 0;
         transition: opacity 0.2s;
         text-align: center;
         @media (max-width: 768px) {
            padding: 0;
         }
      }
      &__title {
         // margin-top: -10px;
         margin: 20px 0;
         @include avalue("font-size", 70, 45);
         line-height: 1;
         font-weight: 300;
         color: #d7ae9c;
         text-transform: uppercase;
      }
      &__text {
         font-family: SaolDisplay;
         @include avalue("font-size", 24, 18);
      }
   }
}
