   &__container {
      display: flex;
      gap: pc(70, $maxWidthContainer);
      @media (max-width: 599px) {
         flex-direction: column-reverse;
         gap: 20px;
      }
   }
   &__text-block {
      flex: 0 0 pc(720, $maxWidthContainer);
   }
   &__header {
      display: flex;
      gap: 10px;
      margin-top: 80px;
      align-items: flex-start;
      @media (max-width: 599px) {
         justify-content: space-between;
         margin-top: 0;
      }
   }
   &__title {
      @include avalue("font-size", 140, 64);
      margin-top: 20px;
      margin-bottom: 30px;
      font-weight: 300;
      font-family: SaolDisplay;
   }
   &__text-content {
      @include avalue("font-size", 26, 20);
   }
   &__media-block {
      position: relative;
      @include avalue("height", 900, 380);
      flex-grow: 1;
   }
   &__image {
      position: absolute;
      flex-shrink: 0;
      img {
         position: absolute;
         border-radius: 50%;
         width: 100%;
         height: auto;
         object-fit: cover;
      }
      &--title {
         position: relative;
         @include size(60);
         padding-bottom: pc(60, 720);
      }
      &--small-left {
         @include asize(110, 50);
         position: absolute;
         bottom: pc(70, 900);
         padding-bottom: pc(110, $maxWidthContainer - 720 - $containerPadding);
         @media (max-width: 599px) {
            top: 0;
            left: 0;
         }
      }
      &--large {
         @include asize(640, 380);
         position: absolute;
         top: pc(130, 900);
         left: pc(120, $maxWidthContainer - 720);
         padding-bottom: pc(640, $maxWidthContainer - 720 - $containerPadding);
         @media (max-width: 390px) {
            transform: translateX(pc(-40, 380));
            left: pc(65, 390);
            top: 0;
            padding-bottom: pc(380, 390);
         }
      }
      &--small-right {
         @include asize(150, 70);
         transform: translateX(50%);
         position: absolute;
         top: pc(200, 900);
         right: 0;
         padding-bottom: pc(150, $maxWidthContainer - 720 - $containerPadding);
         @media (max-width: 1600px) {
            top: 10%;
         }
         @media (max-width: 1439px) {
            display: none;
         }
         @media (max-width: 599px) {
            display: none;
         }
      }
   }
}
