.partners {
   @include avalue("padding-top", 90, 30);
   @include avalue("padding-bottom", 90, 30);
   background-color: #738471;
   & &__container {
    @media (max-width: 390px) {
        padding-left: 15px;
        padding-right: 15px;
    }
   }
   &__title {
      font-family: SaolDisplay;
      @include avalue("font-size", 140, 64);
      font-weight: 300;
      text-align: center;
      margin-bottom: em(70, 140);
      text-transform: lowercase;
   }
   &__list {
      display: flex;
      column-gap: pc(60, $maxWidthContainer);
      flex-wrap: wrap;
      justify-content: center;
   }
   &__list-item {
      @include flex-center;
      position: relative;
      padding-bottom: pc(260, $maxWidthContainer);
      border-bottom: 1px solid rgba($color: #ebebeb, $alpha: 0.3);
      border-top: 1px solid transparent;
      flex: 0 0 calc(25% - pc(math.div(60 * 3, 4), $maxWidthContainer));
      &:nth-child(1), &:nth-child(2)  {
        border-top-color: rgba($color: #ebebeb, $alpha: 0.3);
      }
      @media (min-width: 768.02px) {
        &:nth-child(3), &:nth-child(4)  {
            border-top-color: rgba($color: #ebebeb, $alpha: 0.3);
          } 
      }
      @media (max-width: 768px) {
          flex: 0 0 calc(50% - pc(math.div(60 * 1, 2), $maxWidthContainer));
      }
      img {
         @include abs-center;
         object-fit: cover;
         width: pc(247, 360);
         height: auto;
         mix-blend-mode: screen;
         @media (max-width: 390px) {
            width: 80%;
         }
      }
   }
}
