.locations {
   position: relative;
   & &__container {
      position: relative;
      z-index: 4;
      display: flex;
      padding-top: 90px;
      padding-bottom: 90px;
      @media (max-width: 1439px) {
         display: block;
         padding-top: 100px;
         padding-bottom: 0;
      }
      @media (max-width: 480px) {
        //  padding: 0 0 0 25px;
        padding-right: 0;
      }
      @media (max-width: 389px) {
         padding: 0;
      }
   }
   .bg-image {
      background-color: #363a45;
      @media (max-width: 389px) {
        display: none;
      }
   }
   &__map {
      flex: 0 0 pc(700, $maxWidthContainer);
      position: relative;
      @media (max-width: 1439px) {
         display: none;
      }
   }
   &__map-icon {
      position: absolute;
      cursor: pointer;
      path:nth-child(1) {
         filter: drp-shadow(0px 4px 2px rgba(0, 0, 0, 0.1)) drop-shadow(7px 10px rgba(0, 0, 0, 0.3));
      }
      &[data-map-icon="0"] {
         bottom: pc(305, 900);
         left: pc(530, 700);
      }
      &[data-map-icon="1"] {
         bottom: pc(632, 900);
         left: pc(360, 700);
      }
      &[data-map-icon="2"] {
         bottom: pc(321, 900);
         left: pc(249, 700);
      }
      &.icon-map-with-color,
      &.icon-map-with-color-active {
         position: absolute;
         display: block;
         transform-origin: center bottom;
         transition: transform 0.3s;
         svg {
            filter: drop-shadow(4px 2px rgba(0, 0, 0, 0.1))
               drop-shadow(0 7px 10px rgba(0, 0, 0, 0.3));
         }
         path {
            transition: fill 0.3s, stroke 0.3s;
         }
      }
      &.icon-map-with-color {
         .fill {
            fill: #d7ae9c;
         }
         .inner-path {
            stroke: #363a45;
         }
      }
      &.icon-map-with-color-active {
         transform: scale(math.div(70, 50));
         .fill {
            fill: #181b1e;
         }
         .inner-path {
            stroke: #ebebeb;
         }
      }
   }
   &__slider {
    //   background-color: #181b1e;
      flex: 0 0 pc(920, $maxWidthContainer);
      position: relative;
      min-width: 0;
      overflow-x: clip;
      overflow-y: visible;
      max-width: 990px;
      margin-left: auto;
      margin-right: auto;
      @media (max-width: 1439px) {
         width: 100%;
      }
      @media (max-width: 480px) {
      }
   }
   &__tab-bar {
      flex-wrap: wrap;
      gap: 10px;
      display: none;
      background-color: #000;
      @media (max-width: 1439px) {
         display: flex;
         padding: 25px;
      }
      @media (max-width: 480px) {
        gap: 20px;
      }
   }
   &__tab-button {
      padding: 7px 15px;
      font-size: 12px;
      font-weight: 700;
      background: rgba(255, 255, 255, 0.05);
      border: 1px solid rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(25px);
      text-transform: uppercase;
      color: rgba(#fff, 0.4);
      transition: all 0.3s;
      &[data-tab-active="true"] {
         background-color: #f0bb82;
         color: #363a45;
      }
   }
   &__controls {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      width: calc(100% - 300px - 60px);
      padding: 0 90px 0 70px;
      @media (max-width: 1919px) {
         width: calc(100% - 300px - 30px);
      }
      @media (max-width: 1439px) {
         width: 100%;
         margin-bottom: 0;
         padding: 0;
         background-color: #000;
      }
      .pagination {
         color: #656c7e;
         font-size: 20px;
         &__current-slide {
         }
         &__separator {
         }
         &__slide-count {
         }
         @media (max-width: 1439px) {
            display: none;
         }
      }
      .swiper-navigation {
         display: flex;
         gap: 30px;
         @media (max-width: 1439px) {
            display: none;
         }
         .swiper-button-prev,
         .swiper-button-next {
            width: 80px;
         }
      }
   }
   &__slide {
   }

   .slide {
      color: #fff;
      flex: 0 0 100%;
      background-color: #000;
      display: flex;
      overflow-y: visible;
      overflow-x: clip;
      padding-right: 60px;
      @media (max-width: 1919px) {
         padding-right: 30px;
      }
      @media (max-width: 1439px) {
         flex-direction: column;
      }
      @media (max-width: 430px) {
        padding-right: 0;
      }
      &__text {
         padding: 0 60px 60px 70px;
         margin-top: 60px;
         flex-grow: 1;
         display: flex;
         flex-direction: column;
         @media (max-width: 1439px) {
            padding: 25px;
            flex: auto;
            margin-top: 30px;
         }
         @media (max-width: 768px) {
            margin-top: 0px;
         }
         @media (max-width: 430px) {
            margin-top: 0;
         }
      }
      &__location-name {
         text-transform: uppercase;
         @media (max-width: 1439px) {
            display: none;
         }
      }
      &__title {
         margin-top: 60px;
         color: #d7ae9c;
         @include avalue("font-size", 70, 45);
         line-height: 1;
         margin-bottom: 30px;
         word-break: break-word;
         @media (max-width: 1439px) {
            margin-top: 0;
         }
         @media (max-width: 768px) {
            margin-bottom: 15px;
         }
      }
      &__desc {
         font-size: 15px;
         margin-bottom: 30px;
         p {
            &:not(:last-child) {
               margin-bottom: 20px;
            }
         }
         @media (max-width: 1439px) {
            margin-bottom: 25px;
         }
      }
      &__location {
         opacity: 0.4;
         margin-top: auto;
         display: flex;
         gap: em(15, 21);
         align-items: center;
         @include avalue("font-size", 21, 14);
         &::before {
            font-size: em(27, 21);
         }
         @media (max-width: 1439px) {
            margin-top: auto;
         }
      }
      &__gallery-wrapper {
        //  margin-top: -33px;
        margin-top: 40px;
        padding-bottom: 20px;
         flex: 0 0 300px;
         height: 100%;
         overflow: hidden;
         width: 100%;
         max-width: 100%;
         max-height: 100vh;
         min-height: 0;
         min-width: 0;
         align-self: center;
         @media (max-width: 1439px) {
            margin-top: 0;
            height: auto;
            padding: 0;
            overflow: hidden;
            margin-bottom: 25px;
            flex-shrink: 0;
            padding: 0 25px;
         }
         @media (max-width: 989px) {
            flex-basis: 150px;
            margin-left: 25px;
            padding: 0;
            overflow: hidden;
         }
         @media (max-width: 390px) {
            margin-left: 25px;
         }
         @media (max-width: 389px) {
            margin-left: 15px;
            margin-right: 15px;
         }
      }
      &__gallery {
         width: auto;
         height: 620px;
         flex-shrink: 0;
         display: block;
         max-height: 100%;
         width: 100%;
         padding: 0;
         background-color: transparent;
         overflow: hidden;
         @media (max-width: 1439px) {
            height: auto;
            overflow: hidden;
         }
         @media (max-width: 989px) {
            overflow: visible;
            width: 150 * 5 + 20 * 4px;
         }
         @media (max-width: 550px) {
            width: 150 * 3 + 20 * 2px;
         }
         @media (max-width: 390px) {
             width: 100%;
            max-width: 310px;
            margin-left: auto;
            margin-right: auto;
         }
         @media (max-width: 389px) {
         }
         .swiper-wrapper {
            flex-direction: column;
            @media (max-width: 1439px) {
               flex-direction: row;
            }
         }
         .gallery__image {
            position: relative;
            padding-bottom: 100%;
            flex: 0 0 calc(50% - 5px);
            @media (max-width: 1439px) {
               flex: 0 0 calc(33.3333% - #{math.div(20 * 2, 3)}px);
               padding-bottom: pc(300, 940);
            }
            @media (max-width: 989px) {
               flex: 0 0 calc(20% - #{math.div(20 * 4, 5)}px);
               padding-bottom: 150px;
            }
            @media (max-width: 550px) {
               width: 150px;
               padding-bottom: 150px;
               flex: 0 0 calc(33.3333% - #{math.div(20 * 2, 3)}px);
            }
            @media (max-width: 390px) {
               flex: 0 0 calc(50% - 5px);
            }
            @media (max-width: 389px) {
            //    padding-bottom: pc(150, 310);
               align-self: auto;
            }
            img {
               width: 100%;
               height: auto;
               object-fit: cover;
               position: absolute;
            }
         }
      }
   }
}
