.life {
   background-color: #ebebeb;
   color: #3e4c3d;
   @include avalue("padding-top", 120, 70);
   @include avalue("padding-bottom", 140, 50);
   position: relative;
   text-align: center;
   &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0.8;
      z-index: 1;
      background: no-repeat center
         url('data:image/svg+xml;utf8,<svg width="742" height="712" viewBox="0 0 742 712" fill="none" xmlns="http://www.w3.org/2000/svg"><g opacity="0.1"><path d="M371 1C531.163 1 661 130.837 661 291C661 451.163 531.163 581 371 581C210.837 581 81 451.163 81 291C81 130.837 210.837 1 371 1Z" stroke="%2337473A" stroke-width="2" stroke-miterlimit="10"/><path d="M371 1C531.163 1 661 130.837 661 291C661 451.163 531.163 581 371 581C210.837 581 81 451.163 81 291C81 130.837 210.837 1 371 1Z" stroke="%233E4C3D" stroke-width="2" stroke-miterlimit="10"/><path d="M451 131C611.163 131 741 260.837 741 421C741 581.163 611.163 711 451 711C290.837 711 161 581.163 161 421C161 260.837 290.837 131 451 131Z" stroke="%2337473A" stroke-width="2" stroke-miterlimit="10"/><path d="M451 131C611.163 131 741 260.837 741 421C741 581.163 611.163 711 451 711C290.837 711 161 581.163 161 421C161 260.837 290.837 131 451 131Z" stroke="%233E4C3D" stroke-width="2" stroke-miterlimit="10"/><path d="M291 131C130.837 131 1 260.837 1 421C1 581.163 130.837 711 291 711C451.163 711 581 581.163 581 421C581 260.837 451.163 131 291 131Z" stroke="%2337473A" stroke-width="2" stroke-miterlimit="10"/><path d="M291 131C130.837 131 1 260.837 1 421C1 581.163 130.837 711 291 711C451.163 711 581 581.163 581 421C581 260.837 451.163 131 291 131Z" stroke="%233E4C3D" stroke-width="2" stroke-miterlimit="10"/></g></svg>');
   }
   &__container {
      position: relative;
      z-index: 3;
      display: grid;
      grid-template: auto/repeat(6, 1fr);
      gap: 60px 20px;
      @include avalue("row-gap", 60, 20);
      @media (max-width: 768px) {
         grid-template: auto/auto;
      }
   }
   &__heading {
      font-family: SaolDisplay;
      font-size: 70px;
      @include avalue("font-size", 70, 45);
      line-height: 1;
      grid-column: span 2;
      @media (max-width: 1280px) and (min-width: 769px) {
         grid-area: 1/2/2/6;
      }
      @media (max-width: 768px) {
         grid-column: auto;
         margin-bottom: 30px;
      }
      .line-1 {
      }
      .line-2 {
         font-weight: 300;
         font-style: italic;
      }
      .line-3 {
         font-family: initial;
         font-size: em(50, 70);
         line-height: 1.2;
      }
   }

   &__logo {
      // grid-column: span 2;
      align-self: center;
      justify-self: center;
      fill: #363A45;
      @include abs-center;
      @media (max-width: 1280px) and (min-width: 769px) {
         // grid-area: 3/3/4/5;
         align-self: start;
      }
      @media (max-width: 768px) {
         grid-column: auto;
         fill: #fff;
      }
   }
   &__life-way {
      grid-column: span 2;
      @media (max-width: 768px) {
         grid-column: auto;
      }
      &-1 {
         @media (max-width: 1280px) and (min-width: 769px) {
            grid-area: 2/1/3/4;
            align-self: start;
         }
      }
      &-2 {
         grid-column: 2/4;
         @media (max-width: 1280px) and (min-width: 769px) {
            grid-column: 1/4;
         }
      }
      &-3 {
         grid-column: 4/6;
         @media (max-width: 1280px) and (min-width: 769px) {
            grid-column: 4/8;
         }
      }
      &-2,
      &-3 {
         @media (max-width: 768px) {
            grid-column: auto;
         }
      }
   }
   .image {
      grid-column: span 2;
      @media (max-width: 768px) {
         grid-column: auto;
      }
      img {
         width: 100%;
         height: auto;
      }
   }
   &__right-big-image {
      margin-bottom: pc(-105, 360);
      justify-self: end;
      @media (max-width: 1280px) and (min-width: 769px) {
         grid-area: 2/5/3/7 !important;
         margin-bottom: 0;
      }
      @media (max-width: 768px) {
         justify-self: center;
         margin-bottom: 0;
         position: relative;
      }
   }
   &__left-image {
      @include avalue("width", 240, 140);
      @include avalue("height", 240, 140);
      @media (max-width: 1280px) and (min-width: 769px) {
         grid-area: 3/1/4/2;
      }
      @media (max-width: 768px) {
         justify-self: center;
         display: none;
      }
   }
   &__right-small-image {
      grid-area: 2/5/3/6 !important;
      align-self: end;
      justify-self: self-start;
      @media (max-width: 1280px) and (min-width: 769px) {
         grid-area: 3/6/4/7 !important;
      }
      @media (max-width: 768px) {
         grid-area: auto !important;
         justify-self: center;
         align-self: center;
         grid-row-start: 6 !important;
      }
   }
}
.life-way {
   &__header {
      @include avalue("font-size", 26, 20);
      text-transform: uppercase;
      font-weight: 500;
      margin-bottom: 15px;
      display: inline-flex;
      align-items: center;
      .before {
         display: inline-flex;
         &::after {
            content: "";
            width: em(34, 26);
            height: em(34, 26);
            background: no-repeat center / contain url(@img/circle.png);
            margin: 0 em(20, 26);
            display: inline-block;
         }
      }
      .after {
      }
   }
   &__body {
      font-size: 15px;
      line-height: 1.4;
      @media (max-width: 390px) {
         br {
            display: none;
         }
      }
   }
}
