* {
   box-sizing: border-box;
}
html {
   scroll-behavior: smooth;
}
button {
   border: none;
   background-color: transparent;
   cursor: pointer;
   color: inherit;
}
input,
textarea {
   font-weight: inherit;
   background-color: inherit;
   font-size: inherit;
   line-height: inherit;
   font-weight: inherit;
   &::placeholder {
      font-size: inherit;
   }
   &:-webkit-autofill,
   &:-webkit-autofill:focus {
      transition: background-color 600000s 0s, color 600000s 0s;
   }
}
textarea {
   display: block;
}
a {
   color: inherit;
   &,
   &::before {
      transition: all 0.3s;
   }
}
th {
   text-align: left;
   font-weight: inherit;
}
address {
   font-style: normal;
}
[data-spoiler] {
   cursor: pointer;
}
[class*="image-sprite"] {
   display: none;
}
[class*="__title"] {
   word-wrap: break-word;
}
*::before,
*::after {
   pointer-events: none;
}
.swiper-wrapper {
   display: flex;
}
.swiper-autoheight {
    height: auto;
    .swiper-wrapper {
        align-items: flex-start;
        transition-property: transform, height;
    }
    .swiper-slide {
        height: auto;
    }
}
.italic {
   font-style: italic;
}
.uppercase {
   text-transform: uppercase;
}
.swiper-lazy-preloader {
   display: inline-block;
   width: 80px;
   height: 80px;
   @include abs-center;
   z-index: 0;
   &::after {
      content: " ";
      display: block;
      width: 64px;
      height: 64px;
      margin: 8px;
      border-radius: 50%;
      border: 6px solid rgba(#ebebeb, 0.5);
      border-color: rgba(#ebebeb, 0.5) transparent rgba(#ebebeb, 0.5) transparent;
      animation: lds-dual-ring 1.2s linear infinite;
   }
}
@keyframes lds-dual-ring {
   0% {
      transform: rotate(0deg);
   }
   100% {
      transform: rotate(360deg);
   }
}
.text-content {
   ul {
      li {
         padding-left: em(36 + 24, 20);
         @include avalue("padding-left", 26 + 24, 20 + 15);
         position: relative;
         &::before {
            content: "";
            position: absolute;
            left: 0;
            top: em(14, 20);
            @include avalue("width", 36, 20);
            height: 1px;
            background-color: #ebebeb;
         }
         &:not(:last-child) {
            @include avalue("margin-bottom", 40, 15);
         }
         ul {
            margin-top: 20px;
            li {
               @include avalue("padding-left", 35, 20);
               position: relative;
               &::before {
                  content: "";
                  position: absolute;
                  left: 0;
                  top: em(16, 20);
                  @include avalue("width", 20, 12.5);
                  height: 1px;
                  background-color: #ebebeb;
               }
               &:not(:last-child) {
                  @include avalue("margin-bottom", 20, 10);
               }
            }
         }
      }
   }
}

.swiper-button-next,
.swiper-button-prev {
   cursor: pointer;
   width: 140px;
   height: 11px;
   background: no-repeat no-repeat left center
      url('data:image/svg+xml;utf8,<svg height="11" viewBox="0 0 140 11" fill="none" xmlns="http://www.w3.org/2000/svg"><rect x="11" y="5" width="129" height="1" fill="white" /><path d="M0 5.5L9 0V11L0 5.5Z" fill="white" /></svg>');
}
.swiper-button-prev {
}
.swiper-button-next {
   transform: rotate(180deg);
}
.swiper-navigation.dark {
   .swiper-button-next,
   .swiper-button-prev {
      background-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 140 11" fill="none" xmlns="http://www.w3.org/2000/svg"><rect x="11" y="5" width="129" height="1" fill="%233B3633" /><path d="M0 5.5L9 0V11L0 5.5Z" fill="%233B3633" /></svg>');
   }
}
.bg-image {
   position: absolute;
   inset: 0 0 0 0;
   width: 100%;
   height: 100%;
   object-fit: cover;
   z-index: -1;
}
.round-button {
   border-radius: 50%;
   line-height: 1.3;
   font-weight: 700;
   @include avalue("font-size", 21, 14);
   text-transform: uppercase;
   color: #3b3633;
   @include flex-center;
   padding: 20px;
   word-break: break-word;
   transition: all 0.3s;
   @media (max-width: 400px) {
      padding: 10px;
   }
   @media (any-hover: hover) {
      &:hover {
         transform: scale(1.1);
      }
   }
}
::selection {
   background-color: #738471;
}
._error-list {
   margin-top: rem(5);
   color: red;
   font-size: rem(12);
   min-height: rem(17 * 2);
   opacity: 0;
   transition: opacity 0.3s linear;
   &--visible {
      opacity: 1;
   }
   li:not(:last-child) {
      margin-bottom: rem(7);
   }
}
@keyframes fadeIn {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 1;
      // display: none;
   }
}
.button {
   background: rgba(255, 255, 255, 0.05);
   border: 1px solid rgba(255, 255, 255, 0.15);
   backdrop-filter: blur(25px);
   padding: 15px 25px;
   text-transform: uppercase;
   letter-spacing: 0.05em;
   font-size: 15px;
   line-height: math.div(18, 15);
   font-weight: 700;
}

br.responsive {
   display: none;
}

.nobr {
   white-space: pre;
}
.scroll-to-top {
   border-radius: 50%;
   @include size(60);
   background: rgba(255, 255, 255, 0.1);
   border: 1px solid rgba(255, 255, 255, 0.15);
   backdrop-filter: blur(12px);
   cursor: pointer;
   position: fixed;
   bottom: 50px;
   right: 50px;
   visibility: hidden;
   opacity: 0;
   transition: all 0.3s;
   z-index: 70;
   &::before {
      content: "\e902";
      color: #fff;
      @include flex-center;
      font-size: 20px;
      @extend %ic;
   }
   &--visible {
      visibility: visible;
      opacity: 1;
   }
}
[data-video-playing] {
   .scroll-to-top {
      opacity: 0;
      pointer-events: none;
   }
}
._ibg {
   position: relative;
   img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
   }
}
body.ie {
   ._ibg {
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
   }
   ._ibg img {
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
   }
}

.row:after {
   display: block;
   content: "";
   clear: both;
}
.rub:after {
   content: "\u20BD";
}
%noselect {
   user-select: none;
}
%center {
   display: flex;
   justify-content: center;
   align-items: center;
}
