.whitelist {
   @include avalue("padding-top", 120, 60);
   @include avalue("padding-bottom", 120, 50);
   position: relative;
   &__container {
      display: flex;
      @media (max-width: 760px) {
         flex-direction: column-reverse;
         gap: 15px;
      }
   }
   &__button-block {
      flex-grow: 1;
   }
   .bg-image {
    @media (max-width: 768px) {
        object-position: left top;
    }
   }
   &__text-block {
      flex: 0 0 pc(700, $maxWidthContainer);
      @media (max-width: 760px) {
         margin-left: pc(70, 390);
         font-size: 17px;
      }
   }
   &__button {
      @include asize(300, 160);
      margin-top: pc(240, $maxWidthContainer - 700);
      background: rgba(255, 255, 255, 0.1);
      border: 2px solid rgba(255, 255, 255, 0.15);
      backdrop-filter: blur(25px);
      color: #fff;
      @media (max-width: 760px) {
         margin-top: 0;
      }
   }
   &__title {
      margin-top: 40px;
      @include avalue("font-size", 45, 36);
      line-height: 1;
      font-weight: 300;
      margin-bottom: 50px;
      font-family: SaolDisplay;
        display: flex;
        flex-direction: column;
        gap: 10px;
      .uppercase {
         color: #b5d8d6;
         @include avalue("font-size", 90, 45);
      }
      @media (max-width: 760px) {
         margin-bottom: 30px;
         margin-top: 0;
         margin-left: 40px;
      }
      @media (max-width: 389px) {
        margin-left: 0;
      }
   }
   &__text-content {
      @include avalue("margin-bottom", 50, 30);
   }
   &__list {
   }
   &__social-list {
      display: flex;
      justify-content: end;
      margin-right: pc(140, 700);
      gap: 20px;
      flex-wrap: wrap;
      @media (max-width: 760px) {
         margin-right: pc(25, 390);
         gap: 15px;
      }
   }
   &__social-list-item {
      display: flex;
   }
   &__social-link {
      @include asize(90, 50);
      border-radius: 50%;
      @include flex-center;
      background: rgba(255, 255, 255, 0.1);
      border: 1px solid rgba(255, 255, 255, 0.15);
      backdrop-filter: blur(25px);
      border-radius: 90px;
   }
}
