.video {
   padding-top: .4em;
   padding-bottom: .2em;
   background: linear-gradient(0deg, #3e4c3d, #3e4c3d), #37473a;
   @include avalue("font-size", 140, 64);
   & &__container {
      @media (max-width: 1440px) {
         padding: 0;
      }
   }
   &__wrapper {
      position: relative;
      padding-top: pc(1080, 1920);
   }
   &__video {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      cursor: pointer;
   }
   &__poster {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      pointer-events: none;
      touch-action: none;
      object-fit: cover;
   }
   &__title {
      color: #fff;
      font-family: SaolDisplay;
      line-height: 1;
      font-weight: 300;
      text-align: center;
      width: 100%;
      margin-top: .2em;
   }

   &__button {
      @include abs-center;
      @include asize(120, 70);
      border: none;
      z-index: 95;
      background: url('data:image/svg+xml;utf8,<svg viewBox="0 0 120 120" fill="none" xmlns="http://www.w3.org/2000/svg"><g filter="url(%23filter0_b_263_1277)"><circle cx="60" cy="60" r="60" fill="white" fill-opacity="0.6"/><circle cx="60" cy="60" r="59.5" stroke="white" stroke-opacity="0.3"/></g><path d="M78 60L51 75.5885L51 44.4115L78 60Z" fill="%23363A45"/><defs><filter id="filter0_b_263_1277" x="-24" y="-24" width="168" height="168" filterUnits="userSpaceOnUse" color-interpolation-filters="sRGB"><feFlood flood-opacity="0" result="BackgroundImageFix"/><feGaussianBlur in="BackgroundImageFix" stdDeviation="12"/><feComposite in2="SourceAlpha" operator="in" result="effect1_backgroundBlur_263_1277"/><feBlend mode="normal" in="SourceGraphic" in2="effect1_backgroundBlur_263_1277" result="shape"/></filter></defs></svg>');
      background-color: #fff;
      border-radius: 50%;
      transition: transform 0.3s, opacity .3s;
      @media (any-hover: hover) {
         &:hover {
            transform: translate(-50%, -50%) scale(1.15);
         }
      }
   }
}
