.ms {
   color: #fff;
   text-align: center;
   position: relative;
   min-height: 100vh;
   &::before,
   &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
   }
   &::before {
      top: 0;
      left: 0;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      background-blend-mode: multiply, normal;
   }
   &::after {
      @include abs-center;
      background: no-repeat center
         url('data:image/svg+xml;utf8,<svg viewBox="0 0 1392 1080" fill="none" xmlns="http://www.w3.org/2000/svg"><g opacity="0.3"><path d="M696.208 820.659C974.718 820.659 1200.49 592.407 1200.49 310.844C1200.49 29.2812 974.718 -198.971 696.208 -198.971C417.699 -198.971 191.922 29.2812 191.922 310.844C191.922 592.407 417.699 820.659 696.208 820.659Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M886.712 1151.8C1165.22 1151.8 1391 923.549 1391 641.986C1391 360.423 1165.22 132.171 886.712 132.171C608.203 132.171 382.426 360.423 382.426 641.986C382.426 923.549 608.203 1151.8 886.712 1151.8Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M505.595 1151.8C784.105 1151.8 1009.88 923.549 1009.88 641.986C1009.88 360.423 784.105 132.171 505.595 132.171C227.085 132.171 1.30859 360.423 1.30859 641.986C1.30859 923.549 227.085 1151.8 505.595 1151.8Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/></g></svg>');
      background-size: 100% 100%;
      @media (max-width: 768px) {
         background-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 390 766" fill="none" xmlns="http://www.w3.org/2000/svg"><g opacity="0.15"><path d="M194.893 577.394C352.07 577.394 479.488 448.579 479.488 289.678C479.488 130.778 352.07 1.96289 194.893 1.96289C37.7147 1.96289 -89.7031 130.778 -89.7031 289.678C-89.7031 448.579 37.7147 577.394 194.893 577.394Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M302.4 764.277C459.578 764.277 586.996 635.462 586.996 476.561C586.996 317.66 459.578 188.846 302.4 188.846C145.223 188.846 17.8047 317.66 17.8047 476.561C17.8047 635.462 145.223 764.277 302.4 764.277Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M87.3182 764.277C244.496 764.277 371.914 635.462 371.914 476.561C371.914 317.66 244.496 188.846 87.3182 188.846C-69.8596 188.846 -197.277 317.66 -197.277 476.561C-197.277 635.462 -69.8596 764.277 87.3182 764.277Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/></g></svg>');
      }
   }

   &__container {
      padding-top: 131px;
      padding-bottom: 45px;
      display: grid;
      grid-template: auto/ pc(300, $maxWidthContainer) auto pc(280, $maxWidthContainer);
      align-items: center;
      justify-items: center;
      column-gap: pc(100, $maxWidthContainer);
      position: relative;
      z-index: 1;
      @media (max-width: 1024px) {
         grid-template: auto/repeat(2, auto);
         gap: 40px 0;
      }
   }
   &__title {
      font-family: SaolDisplay;
      font-weight: 300;
      @include avalue("font-size", 140, 56);
      line-height: 1.1;
      grid-row: span 2;
      grid-area: 1/2/3/3;
      text-transform: uppercase;
      z-index: 5;
      @media (max-width: 1024px) {
         grid-area: 2/1/3/3;
      }
      .another-font {
         font-family: UntitledSans;
      }
      .italic {
         font-style: italic;
         text-transform: none;
      }
   }

   &__membership-button {
      @include avalue("width", 250, 110);
      @include avalue("height", 250, 110);
      background-color: #b5d8d6;
      @media (max-width: 1024px) {
         grid-area: 1/1/2/3;
         justify-self: start;
      }
   }
   &__slogan {
      max-width: 250px;
      font-weight: 500;
      @include avalue("font-size", 18, 15);
   }
   &__slogan-1 {
      @media (max-width: 1024px) {
         grid-area: 3/1/4/3;
      }
   }
   &__slogan-2 {
      @media (max-width: 1024px) {
         display: none;
      }
   }
   &__whitelist-button {
      @include avalue("width", 200, 140);
      @include avalue("height", 200, 140);
      background-color: #f0bb82;
      @media (max-width: 1024px) {
         grid-area: 4/2/5/3;
      }
   }
   &__scroll-down-link {
      border-radius: 50%;
      @include size(40);
      background-color: #fff;
      @include flex-center;
      grid-area: 3/2/3/3;
      margin-top: 100px;
      &::before {
         color: #000;
         font-size: 11px;
      }
      @media (max-width: 1024px) {
         grid-area: 4/1/5/2;
         margin-top: 0;
      }
   }
}
