.gallery {
   background-color: #ebebeb;
   @include avalue("padding-top", 90, 30);
   @include avalue("padding-bottom", 90, 30);
   &__container {
   }
   &__list {
      display: grid;
      grid-template: repeat(3, 1fr) / repeat(81, pc(20, $maxWidthContainer));
      row-gap: calc(pc(60, 1080));
      padding-bottom: pc(60 * 2, $maxWidthContainer);
      @media (max-width: 390px) {
         margin: 0 pc(-46, 390 - 50);
         padding-bottom: pc(18 * 2, 390);
      }
   }

   &__image {
      position: relative;
      padding-bottom: pc(360, 360);
      height: 0;
      overflow: hidden;
      img {
         position: absolute;
         width: 100%;
         object-fit: cover;
      }
      &:nth-child(1) {
         grid-column: auto / span math.div(360, 20);
      }
      &:nth-child(2) {
         grid-column: auto / span math.div(780, 20);
         grid-column-start: 18 + 3 + 1;
         padding-bottom: pc(780, $maxWidthContainer);
         padding-bottom: pc(360, 780);
      }
      &:nth-child(3) {
         grid-column: auto / span math.div(360, 20);
         grid-column-start: 18 + math.div(780, 20) + 3 * 2 + 1;
      }
      &:nth-child(4) {
         grid-column: auto / span math.div(500, 20);
         padding-bottom: pc(500, $maxWidthContainer);
         padding-bottom: pc(360, 500);
      }
      &:nth-child(5) {
         grid-column: auto / span math.div(360, 20);
         grid-column-start: math.div(500, 20) + 3 + 1;
      }
      &:nth-child(6) {
         grid-column: auto / span math.div(640, 20);
         grid-column-start: math.div(500, 20) + math.div(360, 20) + 3 * 2 + 1;
         padding-bottom: pc(640, $maxWidthContainer);
         padding-bottom: pc(360, 640);
      }
      &:nth-child(7) {
         grid-column: auto / span math.div(360, 20);
      }
      &:nth-child(8) {
         grid-column: auto / span math.div(640, 20);
         grid-column-start: math.div(360, 20) + 3 + 1;
         padding-bottom: pc(640, $maxWidthContainer);
         padding-bottom: pc(360, 640);
      }
      &:nth-child(9) {
         grid-column: auto / span math.div(500, 20);
         grid-column-start: math.div(360, 20) + + math.div(640, 20) + 3 * 2 + 1;
         padding-bottom: pc(500, $maxWidthContainer);
         padding-bottom: pc(360, 500);
      }
   }
}
