.pillars {
   position: relative;
   z-index: 1;
   background-color: #ebebeb;
   @include avalue("padding-top", 120, 70);
   @include avalue("padding-bottom", 120, 30);
   &::before,
   &::after {
      content: "";
      position: absolute;
      z-index: -1;
   }
   &::before {
      left: 0;
      top: 333px;
      width: 770 - 295px;
      height: 770px;
      opacity: 0.6;
      background: no-repeat
         url('data:image/svg+xml;utf8,<svg width="auto" height="auto" viewBox="0 0 772 772" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M90 771C302.63 771 475 598.63 475 386C475 173.37 302.63 1 90 1C-122.63 1 -295 173.37 -295 386C-295 598.63 -122.63 771 90 771Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M67 471C188.503 471 287 372.503 287 251C287 129.497 188.503 31 67 31C-54.5027 31 -153 129.497 -153 251C-153 372.503 -54.5027 471 67 471Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/></svg>');
      @media (max-width: 1024px) {
         top: 10%;
      }
      @media (max-width: 700px) {
         transform: scaleX(-1);
         top: 40px;
         width: pc(240, 390);
         right: 0;
         left: auto;
         opacity: 1;
      }
   }
   &::after {
      right: 0;
      bottom: 74px;
      height: 948px;
      width: 415px;
      opacity: 0.6;
      transform-origin: 100% 100%;
      background: no-repeat
         url('data:image/svg+xml;utf8,<svg width="auto" height="auto" viewBox="0 0 415 948" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M490 441C611.503 441 710 342.503 710 221C710 99.4974 611.503 1 490 1C368.497 1 270 99.4974 270 221C270 342.503 368.497 441 490 441Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M322 947C499.283 947 643 803.283 643 626C643 448.717 499.283 305 322 305C144.717 305 1 448.717 1 626C1 803.283 144.717 947 322 947Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/></svg>');
      @media (max-width: 1280px) {
         transform: scale(0.7);
         bottom: 15px;
      }
      @media (max-width: 700px) {
         opacity: 1;
         // transform: scale(.4);
      }
   }
   &__container {
   }
   &__list {
      color: #363a45;
      margin-bottom: 58px;
      display: grid;
      grid-template: auto/repeat(4, 1fr);
      column-gap: pc(50, $maxWidthContainer);
      @media (max-width: 1024px) {
         grid-template: auto/repeat(2, 1fr);
         row-gap: 40px;
      }
      @media (max-width: 700px) {
         grid-template-columns: auto;
      }
   }
   &__list-item {
   }
   &__title {
      font-size: 120px;
      @include avalue("font-size", 120, 54);
      line-height: 1;
      font-family: SaolDisplay;
      .responsive {
         @media (max-width: 700px) {
            display: block;
         }
      }
   }
}
.pillars-slider {
   & &__container {
      @media (max-width: 1350px) and (min-width: 850px) {
         padding-left: 0;
         padding-right: 0;
      }
      @media (max-width: 390px) {
         padding-left: 15px;
         padding-right: 0;
      }
   }
   &__slider-wrapper {
      width: 150%;
      margin-right: -50%;
      padding-right: 50%;
      overflow: hidden;
      @media (min-width: 2300px) {
         width: 130%;
         margin: 0 -15%;
         padding: 0 15%;
      }
      @media (max-width: 1440px) {
         margin: 0;
         padding: 0;
         width: 100%;
      }
      @media (max-width: 530px) {
         overflow: hidden;
      }
   }
   &__slider {
      color: #fff;
      @media (max-width: 1350px) {
         max-width: 780px;
         margin-left: auto;
         margin-right: auto;
      }
      @media (max-width: 529px) {
         // flex: 0 0 pc(260, 390);
         // width: pc(260, 390 - 50);
         margin: 0;
         width: 260px;
      }
      .swiper-controls {
         height: 100px;
         @media (max-width: 1368px) {
            height: 80px;
         }
         @media (max-width: 920px) {
            height: 75px;
         }
         @media (max-width: 700px) {
            height: 50px;
         }
         @media (max-width: 529px) {
            display: none;
         }
      }
      .swiper-navigation {
         display: flex;
         justify-content: end;
         align-items: center;
         gap: 80px;
         @media (max-width: 700px) {
            justify-content: space-around;
         }
      }
      .swiper-button-prev,
      .swiper-button-next {
      }
      .swiper-button-prev {
      }
      .swiper-button-next {
      }
      .slide {
         flex: 0 0 calc(50% - 30px);
         position: relative;
         background-color: #bdcebb;
         @media (max-width: 1440px) {
            flex: 0 0 calc(50% - 15px);
         }
         @media (max-width: 1350px) {
            flex: 0 0 100%;
         }
         @media (max-width: 1280px) {
         }
         @media (max-width: 699px) {
            flex: 0 0 calc(50% - 7.5px);
         }
         @media (max-width: 529px) {
            flex: 0 0 100%;
         }
         &__desc {
            padding: 30px 30px 40px;
            @include avalue("padding-top", 30, 18);
            @include avalue("padding-left", 30, 18);
            @include avalue("padding-right", 30, 18);
            @include avalue("padding-bottom", 40, 23);
            @include avalue("font-size", 18, 15);
            font-weight: 500;
            border-top: 1px solid rgba(255, 255, 255, 0.15);
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            z-index: 10;
            &::before {
               content: '';
               position: absolute;
               inset: 0 0 0 0;
               background-color: rgba(0, 0, 0, 0.5);
               filter: blur(0);
               z-index: -1;
            }
         }
         &__image {
            position: relative;
            padding-bottom: pc(400, math.div($maxWidthContainer - 60, 2));
            z-index: 5;
            @media (max-width: 699px) {
               padding-bottom: pc(440, 260);
            }
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
               position: absolute;
            }
         }
      }
   }
}

.pillar {
   &__image {
      margin-bottom: 50px;
      width: 100%;
      position: relative;
      img {
         position: absolute;
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
      @media (max-width: 1024px) {
         margin-bottom: 20px;
      }
   }
   &:nth-child(2) {
      // 400 / ((1620 + 30)/4 - 60*3/4)
      .pillar__image {
         padding-bottom: pc(
            400,
            math.div($maxWidthContainer + $containerPadding, 4) - 60 * math.div(3, 4)
         );
         // padding-bottom: pc(400, $maxWidthContainer);
      }
   }
   &:nth-child(3) {
      .pillar__image {
         padding-bottom: pc(
            500,
            math.div($maxWidthContainer + $containerPadding, 4) - 60 * math.div(3, 4)
         );
         // padding-bottom: pc(500, $maxWidthContainer);
      }
   }
   &:nth-child(4) {
      .pillar__image {
         padding-bottom: pc(
            300,
            math.div($maxWidthContainer + $containerPadding, 4) - 60 * math.div(3, 4)
         );
      }
   }
   &:nth-child(2), &:nth-child(3), &:nth-child(4) {
      .pillar__image {
         @media (max-width: 1024px) {
            padding-bottom: pc(
               400,
               math.div($maxWidthContainer + $containerPadding, 4) - 60 * math.div(3, 4)
            );
         }
      }
   }
   &__title {
      font-family: SaolDisplay;
      font-size: 50px;
      margin-bottom: 10px;
   }
   &__desc {
      p:not(:last-child) {
         margin-bottom: 15px;
      }
   }
}
