.team {
   background-color: #363a45;
   @include avalue("padding-top", 120, 70);
   @include avalue("padding-bottom", 90, 70);
   &__container {
   }
   &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 50px;
      @include avalue("font-size", 70, 45);
      margin-bottom: em(50, 70);
   }
   &__title {
      text-transform: uppercase;
      font-weight: 300;
      color: #ebebeb;
      @media (max-width: 900px) {
         text-align: center;
         flex-grow: 1;
      }
   }
   &__slider {
      overflow: hidden;
      // outline: 6px solid wheat;
      @media (max-width: 1619px) {
         margin-left: auto;
         overflow: visible;
         margin-right: auto;
      }
      @media (max-width: 1170px) {
         // width: 930px;
         width: 100%;
      }
      @media (max-width: 960px) {
      }
      @media (max-width: 750px) {
         width: 360px;
         margin: 0 auto;
      }
      @media (max-width: 550px) {
         margin: 0 auto 0 0;
      }
      @media (max-width: 420px) {
         width: 290px;
      }
      @media (max-width: 370px) {
         width: 100%;
      }
   }
   &__slide {
      flex: 0 0 calc(25% - #{math.div(60 * 3, 4)}px);
      @media (max-width: 1619px) {
         flex: 0 0 calc(25% - #{math.div(30 * 3, 4)}px);
      }
      @media (max-width: 1279px) {
         flex: 0 0 calc(33.3333% - #{math.div(30 * 2, 3)}px);
      }
      @media (max-width: 959px) {
         flex: 0 0 calc(50% - 15px);
      }
      @media (max-width: 750px) {
         flex: 0 0 100%;
      }
   }
}
.team {
   .swiper-pagination {
      display: flex;
      align-items: center;
      gap: 12px;
      .swiper-pagination-bullet {
         border-radius: 50%;
         background-color: #fff;
         opacity: 0.2;
         transition: all 0.3s;
         @include size(8);
         &-active {
            transform: scale(pc(10, 8));
            opacity: 1;
         }
      }
      @media (max-width: 900px) {
         display: none;
      }
   }
   .swiper-navigation {
      display: flex;
      gap: 80px;
      align-items: center;
      @media (max-width: 900px) {
         display: none;
      }
   }
   .swiper-button-prev {
      display: flex;
   }
   .swiper-button-next {
      display: flex;
   }
}
.team {
   .member {
      display: flex;
      flex-direction: column;
      &__image {
         position: relative;
         padding-bottom: 100%;
         background: linear-gradient(
               180deg,
               rgba(54, 58, 69, 0) 57.81%,
               rgba(54, 58, 69, 0.28) 79.69%,
               rgba(54, 58, 69, 0.7) 100%
            ),
            linear-gradient(0deg, #47494e, #47494e);
         border-radius: 50%;
         margin-bottom: 20px;
         img {
            border-radius: 50%;
            position: absolute;
            width: 100%;
            height: auto;
         }
      }
      &__name {
         color: #ebebeb;
         text-transform: uppercase;
         line-height: 1;
         @include avalue("font-size", 26, 20);
         margin-bottom: em(15, 26);
         font-weight: 500;
         text-align: center;
      }
      &__text {
         color: #656c7e;
         font-size: 15px;
         margin-bottom: em(23, 15);
      }
      &__link {
         color: #d7ae9c;
         @include avalue("font-size", 24, 20);
         line-height: 1;
         margin-left: auto;
         text-align: right;
         display: flex;
         align-items: flex-end;
         font-family: SaolDisplay;
         margin-top: auto;
         gap: 0.5em;
         &::before {
            font-size: em(20, 24);
         }
      }
   }
}
