.offers {
   color: #ebebeb;
   &__container {
      display: flex;
      align-items: center;
   }
   &__offer-list {
      flex: 0 0 calc(100% - 140px);
      @media (max-width: 1800px) {
         flex: 0 0 100%;
      }
   }
   &__offer {
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: pc(700, $maxWidthContainer - 140) auto;
      &:not(:last-child) {
         margin-bottom: 150px;
         @include avalue("margin-bottom", 150, 50);
      }
      @media (max-width: 767px) {
         grid-template-columns: auto;
      }
      @media (max-width: 390px) {
         grid-template-columns: auto;
      }
   }
   &__slogan {
      writing-mode: vertical-lr;
      color: #ebebeb;
      font-size: 140px;
      line-height: 1;
      font-family: SaolDisplay;
      font-style: italic;
      opacity: 0.1;
      padding: 0 67px;
      white-space: nowrap;
      @media (max-width: 1800px) {
         display: none;
      }
   }
}
.offer {
   @include avalue("margin-top", 90, 70);
   @include avalue("margin-bottom", 120, 30);
   &:last-child {
      margin-bottom: 0;
   }
   &__col {
      border-right: 1px solid rgba(#ebebeb, 0.1);
      @media (max-width: 767px) {
         border-right: none;
      }
   }
   &__title {
      font-family: SaolDisplay;
      @include avalue("font-size", 90, 60);
      font-weight: 400;
      line-height: 1;
      display: inline-block;
      @include avalue("margin-bottom", 50, 0);
      .em {
         color: #a6857e;
      }
      .italic {
         font-style: italic;
         margin-left: 20px;
      }
   }
   &__text {
      max-width: 500px;
      padding-top: 30px;
      @include avalue("font-size", 26, 19);
      ul {
         @include avalue("font-size", 20, 17);
      }
      li {
         position: relative;
         @include avalue("padding-left", 36 + 30, 20 + 15);
         &::before {
            content: "";
            @include avalue("width", 36, 20);
            height: 1px;
            position: absolute;
            left: 0;
            @include avalue("top", 15, 12);
            background-color: #fff;
            margin-right: 30px;
            @include avalue("margin-right", 30, 15);
         }
         &:not(:last-child) {
            margin-bottom: 20px;
         }
      }
   }
   &__image {
      padding-top: 30px;
      .round-image {
         border-radius: 50%;
      }
   }
   .offer__image-container {
      position: relative;
      img {
         position: absolute;
         width: 100%;
         height: 100%;
      }
   }
   // --------------   specific
   &:nth-child(1) {
      .offer__title {
         border-bottom: 1px solid rgba(#ebebeb, 0.1);
         @include avalue("padding-bottom", 85, 20);
      }
      .offer__title {
         @include avalue("font-size", 70, 45);
      }
      .offer__text {
         @include avalue("margin-top", 85, 30);
      }
      .offer__image {
         @include flex-center;
         border-right: 1px solid rgba(#ebebeb, 0.1);
         @media (max-width: 500px) {
            transform: translateX(15%);
         }
      }
      .offer__image-container {
         width: pc(620, $maxWidthContainer - 140 - 700);
         padding-bottom: pc(620, $maxWidthContainer - 140 - 700);
         @media (max-width: 1800px) {
            width: pc(620, $maxWidthContainer - 700);
            padding-bottom: pc(620, $maxWidthContainer - 700);
         }
         @media (max-width: 500px) {
            width: 100%;
            padding-bottom: 100%;
         }
         @media (max-width: 767px) {
            margin-left: auto;
         }
      }
   }
   &:nth-child(2) {
      .offer__title {
         @include avalue("margin-bottom", 100, 0);
      }
      .offer__col {
         border-right: none;
      }
      .offer__content {
         @include avalue("padding-top", 120, 50);
         padding-right: 20px;
         padding-left: 140px;
         position: relative;
         padding-bottom: 50px;
         @media (max-width: 1800px) {
            padding-left: 70px;
         }
         @media (max-width: 1440px) {
            padding-left: 40px;
         }
         @media (max-width: 767px) {
            padding-left: 0;
         }
         &::before,
         &::after {
            content: "";
            position: absolute;
            background-color: rgba(#ebebeb, 0.1);
         }
         &::before {
            width: 1px;
            height: 80%;
            right: 0;
            bottom: 0;
            @media (max-width: 767px) {
               display: none;
            }
         }
         &::after {
            content: "";
            width: 60%;
            height: 1px;
            top: 0;
            @media (max-width: 1800px) {
               width: pc(340, 390);
               left: 50%;
               transform: translateX(-50%);
            }
         }
      }
      .offer__image {
         width: calc(100% + 150px);
         margin-left: -150px;
         grid-area: 1/1/2/2;
         // @media (max-width: 1919px) {
         //    margin-left: 0px;
         //    width: 100%;
         // }
         @media (max-width: 767px) {
            grid-area: auto;
         }
         @media (max-width: 390px) {
            grid-area: auto;
            margin-left: -25px;
         }
      }
      .offer__image-container {
         width: pc(790, $maxWidthContainer - 140 - 700);
         padding-bottom: pc(820, $maxWidthContainer - 140 - 700);
         @media (max-width: 1919px) {
            width: 100%;
            padding-bottom: pc(820, $maxWidthContainer - 700);
         }
         @media (max-width: 1800px) {
            // width: pc(790, $maxWidthContainer - 700);
            padding-bottom: pc(820, $maxWidthContainer - 700);
            width: 100%;
            padding-bottom: calc(100% * 820 / 790);
         }
         @media (max-width: 767px) {
            width: auto;
         }
      }
   }
}
