.header {
   width: 100%;
   position: absolute;
   z-index: 90;
   &__container {
      height: 131px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 480px) {
         height: 82px;
      }
   }
   &__menu {
      @media (max-width: 980px) {
         order: 2;
      }
   }
   &__menu-button {
      display: flex;
      gap: 15px;
      position: fixed;
      padding: 10px 10px 10px 0;
      top: 40px;
      @media (any-hover: hover) {
         &:hover {
            .icon-menu-text {
               // transform: translateX(15px);
            }
         }
      }
      @media (max-width: 980px) {
         top: 30px;
         right: 20px;
         left: auto;
      }
      @media (max-width: 390px) {
         top: 15px;
         left: auto;
         right: 15px;
      }
   }
   &__logo {
      @include abs-center;
      @media (max-width: 480px) {
         height: 38px;
      }
      img {
         height: 100%;
      }
   }
   &__connect-wallet {
      &::before {
         font-size: 22px;
         @extend %ic;
         @media (max-width: 600px) {
            content: "\e90f";
         }
      }
      @media (any-hover: hover) {
         &:hover {
            background-color: rgba(#3e4c3d, 0.5);
         }
      }
      @media (max-width: 980px) {
         order: 1;
      }
      @media (max-width: 600px) {
         border: none;
         background: none;
         padding: 0;
      }
      span {
         @media (max-width: 600px) {
            display: none;
         }
      }
   }
   &__burger {
      &::before {
         content: "";
         width: 100%;
         height: 100%;
      }
      .menu--open & {
      }
      .popup-inner {
         max-height: none;
         position: relative;
         background: linear-gradient(0deg, rgba(54, 58, 69, 0.96), rgba(54, 58, 69, 0.96));
         &::before {
            content: "";
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            background: no-repeat right center / contain url(@img/header/circles.svg);
            height: 100%;
            pointer-events: none;
         }
         &::after {
            content: "";
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            height: 100%;
            width: 100%;
            pointer-events: none;
            z-index: -1;
         }
         & > * {
            padding-left: max(25px, pc(50, $maxWidth));
            padding-right: max(25px, pc(50, $maxWidth));
         }
      }
      .popup-body {
         display: flex;
        //  padding-top: 37px;
         padding-bottom: 100px;
         @media (max-width: 1024px) {
            column-gap: 40px;
         }
         @media (max-width: 600px) {
            flex-direction: column;
            row-gap: 80px;
            padding-bottom: 37px;
         }
      }
   }
   &__burger-header {
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: space-between;
   }
   &__burger-circles {
      font-size: 56px;
   }
   &__burger-close-button {
      background: rgba(255, 255, 255, 0.1);
      border: 1px solid rgba(255, 255, 255, 0.15);
      backdrop-filter: blur(25px);
      @include size(60);
      border-radius: 50%;
      position: fixed;
      right: 0;
      top: 0;
      transform: translate(-100%, 50%);
      @media (max-width: 1023.98px) {
         transform: translate(-50%, 50%);
      }
      @media (max-width: 390px) {
         position: relative;
         transform: none;
      }
      &::before,
      &::after {
         content: "";
         width: 2px;
         height: 20px;
         background-color: #fff;
         position: absolute;
         left: 50%;
         top: 50%;
      }
      &::before {
         transform: translate(-50%, -50%) rotate(45deg);
      }
      &::after {
         transform: translate(-50%, -50%) rotate(-45deg);
      }
   }
   &__nav {
      padding-left: pc(240 - 50, $maxWidth);
      flex: 0 0 calc(50% - (240px - 50px));
      font-family: SaolDisplay;
      @include avalue("font-size", 50, 30);
      line-height: 1;
      color: rgba(#fff, 0.4);
      @media (max-width: 1280px) {
         padding-left: 0;
         flex: 0 0 calc(50% - 20px);
      }
      ul {
         display: flex;
         flex-direction: column;
         gap: 30px;
      }
      &-link {
         transition: color 0.3s;
         display: block;
         @media (any-hover: hover) {
            &:hover {
               color: #fff;
            }
         }
         &--active {
            color: #fff;
         }
      }
   }
   &__contacts {
      margin-left: 240px;
      flex: 0 0 calc(50% - (pc(240, $maxWidth)));
      @media (max-width: 1280px) {
         margin-left: 0;
         flex: 0 0 calc(50% - 20px);
      }
   }
   &__burger-footer {
      border-top: 1px solid rgba($color: #fff, $alpha: 0.1);
      height: 76px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 15px;
      line-height: math.div(21, 15);
      font-weight: 500;
      text-transform: uppercase;
      padding: 0 40px;
      color: rgba($color: #fff, $alpha: 0.4);
   }
   &__copyright {
   }
}

.contacts {
   display: flex;
   flex-direction: column;
   row-gap: 120px;
   font-size: 18px;
   line-height: math.div(25, 18);
   font-weight: 500;
   @media (max-width: 600px) {
      flex-direction: row;
      gap: 60px 30px;
      flex-wrap: wrap;
   }
   &__block {
      @media (max-width: 600px) {
         flex: 0 0 calc(50% - 15px);
      }
      &:last-child {
         margin-top: auto;
         @media (max-width: 600px) {
            margin-top: 0;
         }
      }
   }
   &__block-title {
      margin-bottom: 53px;
      @media (max-width: 600px) {
         margin-bottom: 18px;
      }
   }
   &__block-list {
      display: flex;
      flex-direction: column;
      row-gap: 33px;
      @media (max-width: 600px) {
         row-gap: 18px;
      }
   }
   &__block-link--active {
   }
   &__contacts-list {
   }
   &__block-list-item {
   }
   &__socials-list {
   }
   &__socials-list-item {
   }
   &__links-list {
   }
   &__main-contacts {
      @media (max-width: 600px) {
         flex-grow: 2;
      }
      a {
         font-weight: 400;
         font-size: 20px;
         line-height: 1.4;
         text-decoration: underline;
      }
   }
}

.icon-menu {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   position: relative;
   width: 24px;
   height: 17px;
   flex-shrink: 0;
   cursor: pointer;
   z-index: 5;
   @media (max-width: 980px) {
      width: 40px;
      height: 32px;
   }
   span,
   &::before,
   &::after {
      content: "";
      @include transition;
      height: 3px;
      background-color: #fff;
      @media (max-width: 980px) {
         height: 5px;
         box-shadow: 1px 1px 4px rgba(0,0,0,0.3);
      }
      @media (max-width: 390px) {
         height: 4px;
      }
   }
   &::before {
      width: 100%;
   }
   &::after {
      width: pc(20, 24);
      @media (max-width: 980px) {
         width: 100%;
      }
   }
   span {
      width: pc(9, 24);
      @media (max-width: 980px) {
         width: 100%;
      }
   }
   &-text {
      font-weight: 700;
      font-size: 15px;
      line-height: math.div(18, 15);
      letter-spacing: 0.05em;
      text-transform: uppercase;
      transition: all 0.3s;
      @media (max-width: 980px) {
         display: none;
      }
   }
}
