.wb {
   background: #363a45;
   background: no-repeat center / 100% 90%
      url('data:image/svg+xml;utf8,<svg width="1002" height="962" viewBox="0 0 1002 962" fill="none" xmlns="http://www.w3.org/2000/svg"><g opacity="0.1"><path d="M501 0.999939C713.63 0.999939 886 173.37 886 386C886 598.63 713.63 771 501 771C288.37 771 116 598.63 116 386C116 173.37 288.37 0.999939 501 0.999939Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M616 191C828.63 191 1001 363.37 1001 576C1001 788.63 828.63 961 616 961C403.37 961 231 788.63 231 576C231 363.37 403.37 191 616 191Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/><path d="M386 191C173.37 191 1 363.37 1 576C1 788.63 173.37 961 386 961C598.63 961 771 788.63 771 576C771 363.37 598.63 191 386 191Z" stroke="white" stroke-width="2" stroke-miterlimit="10"/></g></svg>')
      #363a45;
   color: #fff;
   position: relative;
   @media (max-width: 768px) {
      background-size: 125% 90%;
   }
   &__container {
      display: grid;
      grid-template: auto/ pc(943, $maxWidthContainer) auto;
      column-gap: pc(50, $maxWidthContainer);
      @media (max-width: 670px) {
         grid-template: auto/auto;
      }
   }
   &__image-1 {
      margin-top: pc(-53, $maxWidthContainer - 943 - 50);
      margin-right: pc(90, $maxWidthContainer - 943 - 50);
      grid-area: 1/2;
      justify-self: end;
      align-self: start;
      position: relative;
      width: pc(180, $maxWidthContainer - 943 - 50);
      padding-bottom: pc(300, $maxWidthContainer - 943 - 50);
      img {
         position: absolute;
         width: 100%;
         max-height: 100%;
         height: auto;
      }
      @media (max-width: 670px) {
         grid-area: auto;
         margin-top: 0;
         margin-right: pc(50 - 25, 390);
      }
   }
   &__heading {
      font-family: SaolDisplay;
      margin-top: pc(200, 943);
      @include aminvalue("font-size", 120, 54, 600);
      line-height: 1;
      line-height: math.div(120 + 26 * 2, 120);
      grid-area: 1/1;
      align-self: end;
      .italic {
         font-weight: 300;
      }
      .responsive {
         @media (max-width: 670px) {
            display: inline;
         }
      }
      span {
         &:not(:last-child) {
            margin-bottom: 26px;
         }
      }
      @media (max-width: 1024px) {
         margin-top: 9%;
      }
      @media (max-width: 670px) {
         grid-area: auto;
         margin-top: -15%;
      }
   }
   &__text {
      @include avalue("font-size", 26, 20);
      max-width: 410px;
      grid-area: 2/2;
      margin-top: pc(-50, $maxWidthContainer - 943 - 50);
      padding: 1em;
      @media (max-width: 1024px) {
         margin-top: 0;
         align-self: center;
      }
      @media (max-width: 670px) {
         grid-area: auto;
         max-width: none;
      }
   }
   &__image-2 {
      margin-right: pc(305, $maxWidthContainer);
      grid-area: 2/1;
      justify-self: center;
      align-self: center;
      margin-top: pc(114, $maxWidthContainer - 943);
      margin-bottom: pc(110, $maxWidthContainer - 943);
      width: 160px;
      height: 160px;
      img {
         width: 100%;
         height: auto;
      }
      @media (max-width: 670px) {
         grid-area: auto;
         margin-top: 5%;
         margin-bottom: 10%;
         width: 110px;
         height: 110px;
      }
   }
}
