.cryptoaccess {
   @include avalue("padding-top", 120, 50);
   @include avalue("padding-bottom", 120, 30);
   position: relative;
   &::before {
      content: "";
      width: pc(1454, 1920);
      height: pc(780, 900);
      position: absolute;
      bottom: 0;
      right: 0;
      background: no-repeat bottom right / contain url(@img/cryptoaccess/circles.svg);
   }
   &__container {
      @media (max-width: 768px) {
         background: none;
      }
   }
   &__header {
      margin-bottom: 80px;
      display: flex;
      gap: 40px;
      @media (max-width: 1024px) {
         flex-direction: column;
         margin-bottom: 29px;
         max-width: 600px;
         margin-left: auto;
         margin-right: auto;
         text-align: center;
      }
   }
   &__title {
      font-family: SaolDisplay;
      @include avalue("font-size", 50, 36);
      line-height: 1;
      color: #738471;
      margin-top: 30px;
      flex: 0 0 pc(725, $maxWidthContainer);
      text-transform: uppercase;
      .italic {
         font-weight: 300;
         margin-left: 0.15em;
         text-transform: none;
      }
   }
   &__buttons {
      display: flex;
      justify-content: center;
      gap: 32px;
      flex-wrap: wrap;
      flex: 0 0 pc(895, $maxWidthContainer);
      @media (max-width: 480px) {
         justify-content: start;
      }
   }
   &__button {
      font-size: 24px;
      line-height: 1;
      font-family: SaolDisplay;
      color: #d7ae9c;
      text-decoration: underline;
      text-underline-offset: 4px;
      transition: all 0.3s;
      &[data-tab-active] {
         color: #ebebeb;
         text-decoration-color: transparent;
      }
      @media (max-width: 1024px) {
         font-size: 20px;
      }
   }
   &__slider {
      overflow: hidden;
      @media (max-width: 1024px) {
         width: 100%;
         max-width: 600px;
         margin-left: auto;
         margin-right: auto;
      }
      @media (max-width: 480px) {
         overflow: visible;
      }
   }
   &__slide {
   }
   .slide {
      display: flex;
      flex: 0 0 100%;
      @media (max-width: 1024px) {
         flex-direction: column-reverse;
         opacity: 0.5;
         transition: opacity 0.3s;
         &.swiper-slide-active {
            opacity: 1;
         }
      }
      &__plan {
         flex: 0 0 pc(725, $maxWidthContainer);
         @media (max-width: 1024px) {
            flex: auto;
         }
      }
      &__media {
         flex: 0 0 pc(895, $maxWidthContainer);
         display: flex;
         align-items: flex-start;
         justify-content: center;
         @media (max-width: 1024px) {
            flex: 0 1 auto;
         }
      }
      &__image {
         position: relative;
         padding-bottom: pc(220, 340);
         width: 100%;
         img {
            position: absolute;
            width: 100%;
            height: auto;
         }
      }
   }
   .popup {
      color: #ebebeb;
      @include avalue("font-size", 20, 14);
      &-inner {
         max-width: min(1000px, 80%);
         background-color: #363a45;
         margin: 20px auto;
         position: relative;
         @media (max-width: 1024px) {
            max-width: none;
            margin: 0;
         }
      }
      &-header {
         padding: em(90, 20) em(70, 20) 0;
         @include avalue("padding-left", 90, 30);
         @include avalue("padding-right", 90, 30);
         @include avalue("padding-top", 70, 30);
      }
      &-title {
         font-size: em(70, 20);
         font-family: SaolDisplay;
         text-transform: lowercase;
      }
      &-close-button {
         @include asize(90, 45);
         position: absolute;
         right: em(30, 20);
         top: em(30, 20);
         background: rgba(255, 255, 255, 0.1);
         border: 1px solid rgba(255, 255, 255, 0.15);
         backdrop-filter: blur(25px);
         border-radius: 50%;
         &:before,
         &:after {
            content: "";
            width: 36.6%;
            height: 2px;
            background-color: #ebebeb;
            position: absolute;
            top: 50%;
            left: 50%;
         }
         &::before {
            transform: translate(-50%, -50%) rotate(45deg);
         }
         &:after {
            transform: translate(-50%, -50%) rotate(-45deg);
         }
      }
      &-body {
         padding: em(50, 20) em(70, 20) em(80, 20);
         @include avalue("padding-left", 70, 30);
         @include avalue("padding-right", 70, 30);
         @include avalue("padding-top", 50, 30);
         @include avalue("padding-bottom", 80, 30);
         background-color: #363a45;
      }
   }

   .plan {
      display: flex;
      flex-direction: column;
      @media (max-width: 1024px) {
         padding-top: 70px;
         position: relative;
      }
      &__title {
         font-family: SaolDisplay;
         @include avalue("font-size", 90, 50);
         color: #fff;
         br {
            display: none;
         }
         @media (max-width: 1024px) {
            margin-bottom: 30px;
            br {
               display: block;
            }
         }
      }
      &__details {
         margin-bottom: 20px;
         display: flex;
         gap: 40px;
         flex-wrap: wrap;
         align-items: flex-start;
         @media (max-width: 1024px) {
            margin-bottom: 40px;
         }
      }
      &__price {
         @include avalue("price", 50, 36);
         line-height: 1.2;
         color: #d7ae9c;
      }
      &__button {
         color: #3b3633;
         flex-shrink: 0;
         background-color: #f0bb82;
         padding: em(32, 21);
         @include avalue("width", 200, 120);
         @include avalue("height", 200, 120);
         @media (max-width: 1024px) {
            position: absolute;
            right: pc(50, 390);
            top: 0;
            transform: translateY(pc(-25, 120));
            z-index: 3;
         }
      }
      &__date-block {
         font-family: SaolDisplay;
         margin-top: auto;
         @media (max-width: 1024px) {
            padding: 30px 0;
            border-top: 1px solid rgba(#ebebeb, 0.1);
         }
      }
      &__date-title {
         color: #738471;
         font-size: 15px;
         margin-bottom: 15px;
      }
      &__date {
         @include avalue("font-size", 90, 36);
         color: #d7ae9c;
         line-height: 1;
         font-weight: 300;
         font-style: italic;
      }
   }
}
