@use "sass:math";
@import "common/mixins";
@import "common/reset";

$fontFamily: UntitledSans;
$fontSize: 20;
$minFontSize: 14;
$lineHeight: 28;
$background: #3e4c3d;
$mainTextColor: #fff;
$fontWeight: 400;

$minWidth: 390;
$maxWidth: 1920;
$maxWidthContainer: $maxWidth - 150 * 2;
$containerPadding: 30;
$minContainerPadding: 25 * 2;

body {
   font-family: $fontFamily;
   @include avalue("font-size", $fontSize, $minFontSize);
   line-height: math.div($lineHeight, $fontSize);
   font-weight: $fontWeight;
   color: $mainTextColor;
   background-color: $background;
   &._lock {
      overflow: hidden;
   }
}
.wrapper {
   width: 100%;
   min-height: 100vh;
   overflow: hidden;
   display: flex;
   flex-direction: column;
}
.main {
   flex: 1 1 auto;
}

[class*="__container"] {
   max-width: $maxWidthContainer + px;
   box-sizing: content-box;
   margin: 0 auto;
   padding: 0 math.div($containerPadding, 2) + px;
   @media (max-width: #{$minWidth}px) {
      padding: 0 math.div($minContainerPadding, 2) + px;
   }
}

