.pswp .pswp__preloader {
   //   opacity: 0.85 !important;
   position: fixed;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   @include flex-center;
   transition: opacity 0.3s;
   opacity: 0;
   width: 100px;
   height: 100px;
   @media (max-width: 480px) {
      width: 50px;
      height: 50px;
   }
   .pswp__icn {
      display: none;
   }
   &::before {
      content: "";
      height: 100%;
      width: 100%;
      border-radius: 50%;
      --opacity: 0.6;
      border: 10px solid rgba(#ebebeb, var(--opacity));
      border-color: rgba(#ebebeb, var(--opacity)) transparent rgba(#ebebeb, var(--opacity))
         transparent;
      animation: lds-dual-ring 1.2s linear infinite;
   }
   &--active {
      opacity: 1;
   }
}
.pswp__counter {
   margin-right: auto;
   display: none;
}
